import Icon from '../../Common/Icon'
import Spinner from '../../Common/Spinner'
import { Control } from '../Controls'

import styles from './styles.module.css'

export function GitHubCompactPicker ({ choices, selections, state, onExpand }) {
  const { keyboardChoices } = choices
  const { selectedRepoChoice } = selections
  const { selectedBranchName, selectedKeyboard, loading } = state

  return (
    <div onClick={onExpand}>
      <div className={styles.compactWrapper}>
        <Control id="compact-github-picker" style={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          lineHeight: 'normal'
        }}>
          <span>{selectedRepoChoice.name}</span>
          <span style={{ marginLeft: '10px', fontSize: '65%' }}>
            <Icon name="code-branch" />{selectedBranchName}
          </span>
        </Control>
        <Control style={{ }}>
          / {loading ? <Spinner /> : (selectedKeyboard || keyboardChoices[0]?.name)}
        </Control>
      </div>
    </div>
  )
}
