import PropTypes from 'prop-types'

import Link from '../Common/Link'
import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'
import IconButton from '../Common/IconButton'

function SuggestRepoSelection ({ installation }) {
  const url = `https://github.com/settings/installations/${installation.id}`

  return (
    <Modal>
      <DialogBox style={{ maxWidth: 'min-content' }} dismissText={null}>
        <h2>App Disabled</h2>
        <p>
          Hello there, your installation of the keymap editor's GitHub app is
          currently disabled. <em>This is not a ban!</em>
        </p>
        <p>
          When you installed this app for your GitHub account you installed it
          for <em>all</em> of your repositories, and we need to do a little
          cleanup as we roll out some new features as it's very likely you will
          have other repositories unrelated to ZMK keyboard configurations.
        </p>
        <h3>How to fix this</h3>
        <p>
          If you're still interested in using the keymap editor (and I'd love
          for you to be able to) please go to your <Link href={url}>installation
          settings</Link> on GitHub and select only the repositories you want to
          use with this tool. As soon as that's done you'll be sent back here
          and ready to go.
        </p>
        <div style={{ display: 'flex', gap: '20px', textAlign: 'center' }}>
          <div>
            <p>
              <img alt="Example correct GitHub installation selection" src="repo-selection-good.png" />
            </p>
            <p>
              <strong>Do this!</strong>
            </p>
          </div>

          <div>
            <p>
              <img alt="Example incorrect GitHub installation selection" src="repo-selection-bad.png" />
            </p>
            <p>
              <strong>Don't do this!</strong>
            </p>
          </div>
        </div>
        <IconButton
          collection="brands"
          icon="github"
          text="Fix Repository Selection"
          style={{ display: 'block', margin: '0 auto', fontSize: '120%', padding: '5px 10px' }}
          onClick={() => { window.location.href = url }}
        />
        <p>
          As always, if you're looking for clarification or further assistance
          please visit our GitHub repo and open an issue or start a discussion.
        </p>
      </DialogBox>
    </Modal>
  )
}

SuggestRepoSelection.propTypes = {
  installation: PropTypes.shape({
    id: PropTypes.number.isRequired
  }).isRequired
}

export default SuggestRepoSelection
