import DialogBox from '../../../Common/DialogBox'
import Modal from '../../../Common/Modal'
import { GeneralHelpOfferCopy } from '../../../Dialogs/support'
import Link from '../../../Common/Link'

const zmkConfigSetupUrl = 'https://zmk.dev/docs/user-setup#user-config-setup-script'
const zmkConfigLink = (
  <Link external url={zmkConfigSetupUrl}>User Config Setup Script</Link>
)

export default function EmptyRepo (props) {
  const { onDismiss } = props

  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>Empty Repository</h2>
        <p>
          The selected repository doesn't appear to contain anything yet. please
          be sure to use ZMK's {zmkConfigLink} to initialize your repo first.
        </p>
        {GeneralHelpOfferCopy}
      </DialogBox>
    </Modal>
  )
}
