import DialogBox from '../../../Common/DialogBox'
import Modal from '../../../Common/Modal'
import IconButton from '../../../Common/IconButton'
import github from '../api'

function Install () {
  const greeting = github.username
    ? `Welcome, ${github.username}!`
    : 'Welcome!'

  return (
    <Modal centerOnly>
      <DialogBox style={{ maxWidth: 'min-content' }} dismissText={null}>
        <h2>{greeting}</h2>
        <p>
          To begin importing your own keymaps, you'll need to grant the Keymap
          Editor app access to your relevant GitHub repositories. Please make
          sure to <em>only</em> select repositories that are set up for ZMK
          keyboard configurations.
        </p>
        <p>
          This app requires read and write access to the repositories you select
          in order to write to arbitrary <code>.keymap</code> files. It also
          receives all build action events for the repository, which generates
          quite a bit of traffic.
        </p>
        <div style={{ display: 'flex', gap: '20px', textAlign: 'center' }}>
          <div>
            <p>
              <img alt="Example correct GitHub installation selection" src="repo-selection-good.png" />
            </p>
            <p>
              <strong>Do this!</strong>
            </p>
          </div>

          <div>
            <p>
              <img alt="Example incorrect GitHub installation selection" src="repo-selection-bad.png" />
            </p>
            <p>
              <strong>Don't do this!</strong>
            </p>
          </div>
        </div>
        <p>
          Selecting <code>All repositories</code> for the app installation will
          lead to a suspension.
        </p>
        <IconButton
          collection="brands"
          icon="github"
          text="Add Repository"
          style={{ display: 'block', margin: '0 auto', fontSize: '120%', padding: '5px 10px' }}
          onClick={() => github.beginInstallAppFlow()}
        />
      </DialogBox>
    </Modal>
  )
}

Install.propTypes = {}

export default Install
